import {
	getToken,
	removeToken
} from './auth';
import env from './env';

function service(options = {}) {
	options.url = `${env.baseUrl}${options.url}`;
	// token
	if (options.headers.isToken) {
		options.header = {
			'content-type': 'application/json',
			'Authorization': getToken() ? `Bearer ${getToken()}` :
				'eyJhbGciOiJIUzUxMiJ9.eyJsb2dpbl91c2VyX2tleSI6IjI0MjIzZjhjLTEyMmMtNGYwZC1hMmExLWFlNWZhZWE1ZmZkYSJ9.oQIBVdN0828KM8siQjz4vHkzK2N7UC6g7rGm1lPF9bicSmLFw8flavgQWwIgxiIZMmaVU4AUBC6FkhwmRyxtNQ'
		};
	}

	return new Promise((resolved, rejected) => {
		options.success = (res) => {
			// 200
			if (res.data.code !== 200) {
				if (res.data.data[0].code != 0) {
					// 
					uni.showToast({
						icon: 'none',
						duration: 3000,
						title: `${res.msg}`
					});
				}
				// 
				if (res.data.code === 403) {
					// token
					removeToken()
					// 
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}
				// 
				rejected(res)
			} else {
				// 200
				resolved(res.data)
			}
		};
		options.fail = (err) => {
			// 
			uni.showToast({
				icon: 'none',
				duration: 3000,
				title: ','
			});
			rejected(err);
		};
		uni.request(options);
	});
}

export default service;