import Vue from 'vue'
import VueX from 'vuex'
import {
	getadpid,
} from '@/api/tools.js'
Vue.use(VueX)
export default new VueX.Store({
	state: {
		// 
		developers: ["()", "()"],
		// 
		bannerId: "",
		//  
		jLiId: '',
		// 
		InterstitialId: '',
		// (titleTextVuex)
		navigationBarTitleText: "",
		wxAppid: '',
		ttAppid: '',
		ksAppid: '',
		qqAppid: '',
		clientType: null,
		baseUrl: ''
	},
	getters: {
		getBannerId(state) {
			console.log(state);
			return state.bannerId;
		},
		getJliId(state) {
			return state.jLiId;
		}
	},
	mutations: {
		setbannerId(state, payload) {
			state.bannerId = payload
		},
		setjLiId(state, payload) {
			state.jLiId = payload
		},
		setClientType(state, payload) {
			state.clientType = payload
		},
		setBaseUrl(state, payload) {
			state.baseUrl = payload
		},

	},
	actions: {
		async getAdIds(content) {

			// TODO:id,state
			return new Promise((resolve, reject) => {


















				getadpid(content.state.ttAppid).then(res => {

					content.commit('setjLiId', content.state.jLiId)
					content.commit('setClientType', 0)
					// console.log(content.state.clientType);
					resolve()
				}).catch(err => {
					reject(err)
				})











			})
		}

	}
})