/**
 * 
 * @class
 * @classdesc 
 * @param {Object} [options={}] - 
 * @param {string} [options.adUnitId] -  ID
 * @param {number} [options.delay=30] - 30s
 * @param {number} [options.intervalTime=60] - 60s
 * @param {Function} [options.onClose] - 
 * @throws {Error}  ID 
 * @example app.vue
 
 import InterstitialAd from '@/components/InterstitialAd/InterstitialAd.js'
 onLaunch: function() {
 			setTimeout(() => {
 				this.InterstitialAd = new InterstitialAd({
 					adUnitId: adUnitId,
 					delay: 30,
 					intervalTime: 30,
 					onClose(res) {
 						console.log('');
 					}
 				})
 			}, 1000 * 5)
 		},
		 QQ 
		 QQ,delay
 */
class InterstitialAd {
	adInstance = null;
	adUnitId = ""; // ID
	delay = 30; // ()
	intervalTime = 60; // ()

	isShowing = false;

	timerId = null;

	counter = 0;

	constructor(options = {}) {
		const {
			adUnitId,
			delay = 30,
			intervalTime = 60,
			onClose
		} = options


		this.adUnitId = adUnitId;
		this.delay = delay;
		this.intervalTime = intervalTime;
		this.closeHandler = onClose

		this.create(adUnitId);

		this.init()

		if (this.adInstance == null) throw new Error('')
	}


	create(adUnitId = this.adUnitId) {
		if (this.adInstance !== null) throw new Error("")

		/* QQ */








		this.adInstance = tt.createInterstitialAd({
			adUnitId
		});









	}


	init() {
		this.adInstance.onLoad(() => {
			console.log('onLoad event emit');
			if (this.counter == 0) this.delayPlay(this.delay)
		});

		this.adInstance.onError((err) => {
			console.error('onError event emit', err);
			this.delayPlay()
		});

		this.adInstance.onClose((res) => {
			console.log('onClose event emit', res);

			this.delayPlay()
			if (typeof this.closeHandler === 'function') this.closeHandler(res)
		});
	}


	play() {
		// 
		this.isPause = false;

		// 
		this.adInstance.show().catch(err => {
			console.error(err);
			this.delayPlay()
		});

		this.counter++;
	}


	delayPlay(delay = this.intervalTime) {
		setTimeout(this.play.bind(this), 1000 * delay)
	}


	pause() {
		// 
		this.isPause = true

		clearTimeout(this.timerId)
		this.timerId = null;
	}

	closeHandler = function() {}
}

export default InterstitialAd;